.list {
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    margin-top: -30px;
    margin-left: -30px;
    padding: 0;
  }
  
  .item {
    margin-top: 30px;
    margin-left: 30px;
    width: 270px;
    flex-basis: calc((100% - 120px) / 4);
    .title {
      width: 100%;
      text-align: center;
    }
  
    .link {
      width: 100%;
      text-decoration: none;
      color: black;
    }
  
    .img {
      width: 280px;
      height: 400px;
      display: block;
    }
  }
  
  .form {
    display: inline-flex;
    justify-content: center;
    width: 100%;
    margin-bottom: 30px;
  }
  .input {
    width: 700px;
    margin-right: 30px;
    outline: none;
    border: 2px solid black;
    border-radius: 15px;
    text-align: center;
    font-size: 20px;
  }
  
  .searchBtn {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    appearance: none;
    background-color: blue;
    border: 2px solid #1a1a1a;
    border-radius: 15px;
    box-sizing: border-box;
    color: #ffffff;
    cursor: pointer;
    font-size: 16px;
    font-weight: 600;
    line-height: normal;
    margin: 0;
    height: 40px;
    min-width: 0;
    outline: none;
    // padding: 16px 24px;
    text-align: center;
    text-decoration: none;
    transition: all 300ms cubic-bezier(0.23, 1, 0.32, 1);
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
    width: 200px;
    will-change: transform;
    &:disabled {
      pointer-events: none;
    }
  
    &:hover {
      box-shadow: rgba(0, 0, 0, 0.25) 0 8px 15px;
      transform: translateY(-2px);
    }
  
    &:active {
      box-shadow: none;
      transform: translateY(0);
    }
  }
  
  .GoBackBtn {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    appearance: none;
    background-color: blue;
    border: 2px solid #1a1a1a;
    border-radius: 15px;
    box-sizing: border-box;
    color: #ffffff;
    cursor: pointer;
    font-size: 16px;
    font-weight: 600;
    line-height: normal;
    margin-right: 30px;
    height: 40px;
    min-width: 0;
    outline: none;
    // padding: 16px 24px;
    text-align: center;
    text-decoration: none;
    transition: all 300ms cubic-bezier(0.23, 1, 0.32, 1);
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
    width: 200px;
    will-change: transform;
    &:disabled {
      pointer-events: none;
    }
  
    &:hover {
      box-shadow: rgba(0, 0, 0, 0.25) 0 8px 15px;
      transform: translateY(-2px);
    }
  
    &:active {
      box-shadow: none;
      transform: translateY(0);
    }
  }